const blog = {
  state: {
    query: {
      page: 1,
      pageSize: 10,
      pageNumber: 1
    },
    searchKey: '',
    blogs: [],
    shouldRestoreState: false,
    scrollPosition: 0 // 新增滚动位置
  },
  mutations: {
    SET_BLOG_QUERY: (state, query) => {
      state.query = query
    },
    SET_SEARCH_KEY: (state, searchKey) => {
      state.searchKey = searchKey
    },
    SET_BLOGS: (state, blogs) => {
      state.blogs = blogs
    },
    SET_SHOULD_RESTORE_STATE: (state, shouldRestore) => {
      state.shouldRestoreState = shouldRestore
    },
    SET_SCROLL_POSITION: (state, position) => { // 新增滚动位置mutation
      state.scrollPosition = position
    }
  },
  actions: {
    setBlogQuery({ commit }, query) {
      commit('SET_BLOG_QUERY', query)
    },
    setSearchKey({ commit }, searchKey) {
      commit('SET_SEARCH_KEY', searchKey)
    },
    setBlogs({ commit }, blogs) {
      commit('SET_BLOGS', blogs)
    },
    setShouldRestoreState({ commit }, shouldRestore) {
      commit('SET_SHOULD_RESTORE_STATE', shouldRestore)
    },
    setScrollPosition({ commit }, position) { // 新增滚动位置action
      commit('SET_SCROLL_POSITION', position)
    }
  }
}

export default blog



